@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

// Header
.App-header {
  display: flex;
  justify-content: center;
  padding: 5px 0;
  background-color: #282c34;

  .App-logo_wrap {
    display: flex;
    align-items: center;

    .App-logo {
      height: 20px;
      pointer-events: none;
    }

    @media (prefers-reduced-motion: no-preference) {
      .App-logo {
        animation: App-logo-spin infinite 20s linear;
      }
    }

    .App-logo_title {
      font-family: 'Montserrat';
      font-size: 18px;
      font-weight: 600;
      color: white;
    }
  }
}

// Main
.main {
  .categoryWrap {
    display: flex;
    flex-wrap: wrap;
    padding: 40px 20px;
    // background-color: skyblue;

    @media screen and (min-width: 768px) {
      padding: 40px 40px;
    }

    @media screen and (min-width: 1200px) {
      padding: 50px 50px;
    }

    // Category (section)
    .category {
      margin-bottom: 40px;
      width: 100%;
      // background-color: pink;

      @media screen and (min-width: 768px) {
        width: 215px;

        @media screen and (max-width: 1199px) {
          &:not(:nth-child(3n)) {
            margin-right: 21px;
          }
        }
      }

      @media screen and (min-width: 1200px) {
        width: 260px;

        &:not(:nth-child(4n)) {
          margin-right: 20px;
        }
      }
    }

    .categoryTitle {
      margin-bottom: 10px;
      font-size: 16px;
      font-weight: 600;
    }

    .categoryLink {
      font-size: 14px;
    }
  }
}
