@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
p,
a {
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
  color: rgb(91, 120, 173);
}

a:hover {
  text-decoration: underline;
  color: rgb(56, 98, 177);
}

@media screen and (min-width: 414px){.Container_Container__2k8Pf{margin:0 auto;width:414px}}@media screen and (min-width: 768px){.Container_Container__2k8Pf{width:768px}}@media screen and (min-width: 1200px){.Container_Container__2k8Pf{width:1200px}}

@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.App-header{display:flex;justify-content:center;padding:5px 0;background-color:#282c34}.App-header .App-logo_wrap{display:flex;align-items:center}.App-header .App-logo_wrap .App-logo{height:20px;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-header .App-logo_wrap .App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header .App-logo_wrap .App-logo_title{font-family:'Montserrat';font-size:18px;font-weight:600;color:white}.main .categoryWrap{display:flex;flex-wrap:wrap;padding:40px 20px}@media screen and (min-width: 768px){.main .categoryWrap{padding:40px 40px}}@media screen and (min-width: 1200px){.main .categoryWrap{padding:50px 50px}}.main .categoryWrap .category{margin-bottom:40px;width:100%}@media screen and (min-width: 768px){.main .categoryWrap .category{width:215px}}@media screen and (min-width: 768px) and (max-width: 1199px){.main .categoryWrap .category:not(:nth-child(3n)){margin-right:21px}}@media screen and (min-width: 1200px){.main .categoryWrap .category{width:260px}.main .categoryWrap .category:not(:nth-child(4n)){margin-right:20px}}.main .categoryWrap .categoryTitle{margin-bottom:10px;font-size:16px;font-weight:600}.main .categoryWrap .categoryLink{font-size:14px}

